@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
body{
  font-family: 'Poppins', sans-serif;
}
.cardd {
    transform-style: preserve-3d;
    transition: all .5s ease-in-out;
    width: 100%;
  }
  .cardd:hover {
    transform: rotateY(40deg);
  }
  .cardd .side {
    backface-visibility: hidden;
    border-radius: 6px;
    height: 100%;
    position: absolute;
    overflow: hidden;
    width: 100%;
  }
  .cardd .back {
    background: #eaeaed;
    color: #0087cc;
    line-height: 150px;
    text-align: center;
    transform: rotateY(180deg);
  }
  .notification{
    color: black;
    position: absolute;
    font-size: 14px;
    background-color: red;
    border-radius: 50%;
    border-color: red;
    margin-left: 20px;
    width: 15px;
    height: 15px;
    text-align: center;
    vertical-align: middle;
  }

  @media(max-width:770px) and (min-width:600px){
    #responsive-sidenav{
        display: none;
    }
    #nav-responsive{
        width:100% !important
    }
  }