    background-color: #f7f7f7;
    padding:20px
}
.receive_offer{
    margin-top: 50px;
    
}
.receive_offer a{
    text-decoration: none;
    color:rgb(255, 255, 255);
    background-color: #2ECC71;
    /* border: 2px rgb(252, 251, 251) solid; */
    padding: 10px 15px 10px 15px;
    font-size: 15px;
    border-radius: 50px;
   
}
.receive_offer:hover a{
    color:white;
    font-weight: bolder;
}
